.header-color {
  background: #F0F2F0;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #000C40, #F0F2F0);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #000C40, #F0F2F0); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
  background: #F0F2F0;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #000C40, #F0F2F0);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #000C40, #F0F2F0); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.projects-grid {
   display: flex;
   background-color: #EEF4F5;
}

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 85%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: 'Anton', sans-serif;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 70%;
  margin: auto;
}

.contact-list i {
  font-size: 36px;
  padding-right: 25px;
} 

.banner-text {
  text-align: center;
  background-color: black;
  opacity: .8;
  width: 80%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
  text-align: center;
}

.banner-text hr {
  border-top: 5px dotted grey;
  width: 91%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
  text-align: center
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin: auto;
}

.social-links i {
  font-size: 5em;
  padding: 8px;
}

#linkedin {
  color: #0e76a8;
}

#facebook {
  color: #3b5998;
}

#github {
  color: #FFFFFF;
}

#codesignal {
  color: #09599B;
}

#codeacademy {
  color: #FFFFFF;
}

.cardText {
  text-align: center;
  font-weight: italic;
}

.cardActions {
  text-align: center;
}

.cardActions a {
  color: #1B0286;
}

.cardActions a:hover {
  background-color: #E4E4DC;
  color: #EC0D0D;
  font-weight: bold;
}

.social-links a:hover {
  transition: transform .6s;
  transform: scale(1.2); 
}

section {
  margin-top: 30px;
}

.resume-right-col {
  background: #27221F;
  color: #FFFFFF;
  padding: 2em;
}

